body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

body {
  background-image: url('https://images.unsplash.com/photo-1495195129352-aeb325a55b65?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1700&q=100');
}


#container {
  border-radius: 5%;
  background-image: url('https://images.unsplash.com/photo-1538476623182-1f0361962afe?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80');
  height: 700px;
  width: 1020px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  overflow: auto;
}

#photo {
  height: 480px;
  width: 320px;
  float:left;
  margin-top: 5%;
  margin-left: 4%;
  border: solid;
  border-color: #2e2b23;
}

#bio {
  background-color: #2e2b23;
  height: 500px;
  width: 490px;
  float:right;
  margin-top: 5%;
  margin-right: 5%;
  margin-bottom: 4%;
  text-align:center;
  overflow: auto;

}

#header {
  float:top;
  background-color: #2e2b23;
  height: 50px;
  margin-top: 2%;

}

